
<style scoped lang='scss'>
	#myCanvas{
		border:1px solid black;
	}
</style>


<template>
    <div>
	<canvas ref='myCanvas' id="myCanvas" 
        :width="cvsWidth" 
        :height="cvsHeight"
        @mousedown='start'
        @mousemove='draw'
        @mouseup='finish'
    ></canvas>
	<p></p>
	<button @click="()=>setPenColor('yellow')"></button>
	<button @click="()=>setPenColor('black')"></button>
	<button @click="()=>setPenColor('red')"></button>
	<button @click="()=>setPenColor('green')"></button>
	<button @click="()=>setPenColor('blue')"></button>
	<button @click="()=>setPenColor('pink')"></button>
	<button @click="()=>setPenColor('orange')"></button>
	<p></p>
	<button @click="()=>setPenWidth('thin')"></button>
	<button @click="()=>setPenWidth('mid')"></button>
	<button @click="()=>setPenWidth('thick')"></button>
    </div>
</template>


<script>
export default {
    data() {
        return {
            cvsWidth:860,
            cvsHeight:480,
            isMousePress:false,
            last:null,
            pen:{},
            offset:{

            }
        };
    },
    created(){
        
    },
    mounted(){
        this.initCanvas()
    },
    //props:{},//[]
    methods:{
        pos(event){//
            var ex,ey;
            ex=event.clientX;
            ey=event.clientY;

            if(this.offset.top){
                ey-=this.offset.top
            }
            if(this.offset.left){
                ex-=this.offset.left
            }
            return{
                x:ex,
                y:ey
            }
        },
        initCanvas(){
            let cvs=this.$refs.myCanvas;
            let rectSizer= cvs.getBoundingClientRect()
            this.offset.top=rectSizer.top;
            this.offset.left=rectSizer.left;
            this.pen=cvs.getContext('2d');
            this.pen.lineWidth=1
            this.pen.strokeStyle='blue'
        },
        start(){
            this.isMousePress=true;
        },
        draw(event){
            if(!this.isMousePress){ return};
            var xy=this.pos(event);
            if(this.last!=null){//
                //
                this.pen.beginPath();
                this.pen.moveTo(this.last.x,this.last.y);//
                this.pen.lineTo(xy.x,xy.y);//
                this.pen.stroke();
            }
            this.last=xy;//
        },
        finish(){
            this.isMousePress=false;
            this.last=null;
        },
        setPenWidth(lv='thin'){
            switch(lv){
                case 'thin':this.$set(this.pen,'lineWidth',1);break;
                case 'mid':this.$set(this.pen,'lineWidth',5);break;
                case 'thick':this.$set(this.pen,'lineWidth',10);break;
            }
        },
        setPenColor(color='black'){
            this.$set(this.pen,'strokeStyle',color);
        },
    },
};
</script>