<style
    scoped
    lang='scss'
>
@import '~@/styles/jsonFormatter.scss';
</style>
<template>
    <div class="jsonProductor">
        <div class="titleBox">JSON</div>
        <div class='inputBox'>
            <textarea class='tarea' v-model='tarea'></textarea>
            <button class='turning' @click='mainFunction'></button>
        </div>
        <div class='strictJSON' v-html="output"></div>
        <div class="sizeHolder"></div>
        <div class='strictJSONstr' v-html="output2"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            output:'',
            output2:'',
            tarea:'',
        };
    },
    created(){
        
    },
    mounted(){
        
    },
    //props:{},//[]
    methods:{
        mainFunction(){
            
            if(!this.tarea){
                console.log('')
                this.output='<br /><br />'
                this.output2='<br /><br />'
                return;
            }
            try{
                let vr=(new Function('return '+this.tarea))()
                let vs=JSON.stringify(vr)
                let vss=JSON.stringify(vs)
                console.log('\n\n'+vs,'\n\n','\n\n'+vss)
                this.output='<br /><br />'+ vs
                this.output2='<br /><br />'+vss
            }catch(e){
                console.log(
                    // e.name,
                    // e.message,
                    e.stack
                )
                this.output='<br /><br />'
                this.output2='<br /><br />'
            }
        }
    },
    computed:{
        
    },
    watch:{
        
    },
    filters:{
        
    },
    directives:{
        
    },
    components:{
        
    },
};
</script>